/* ContactMe.css */
.contact-container {
  background: transparent;
  color: #333;
  padding: 80px 20px;
  text-align: center;
  margin-top: 60px;
  position: relative;
  border-top: 1px solid rgba(44, 95, 141, 0.15);
}

.contact-container h2 {
  font-size: 3.5rem;
  margin-bottom: 30px;
  position: relative;
  z-index: 1;
  background: linear-gradient(135deg, #1c4a75 0%, #2c5f8d 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.email {
  font-size: 1.8rem;
  margin: 30px 0;
  position: relative;
  z-index: 1;
  font-weight: 500;
  letter-spacing: 1px;
  color: #2c5f8d;
}

.contact-info {
  border-top: 2px solid rgba(44, 95, 141, 0.25);
  padding-top: 40px;
  margin-top: 40px;
  position: relative;
  z-index: 1;
}

/* 深色模式 */
body.dark-mode .contact-container {
  background: transparent;
  color: #eee;
  border-top-color: rgba(90, 155, 196, 0.2);
}

body.dark-mode .email {
  color: #5a9bc4;
}
