/* 基础样式 - 整體流暢設計 */
.resumepage {
  font-size: 1.2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 0;
}

/* 移除所有卡片效果，改用簡潔的分隔線 */
.education,
.experience,
.projects,
.Certifications {
  padding: 60px 0;
  border-bottom: 1px solid rgba(44, 95, 141, 0.15);
  margin: 0;
  transition: all 0.3s ease;
}

.education:last-child,
.experience:last-child,
.projects:last-child,
.Certifications:last-child {
  border-bottom: none;
}

/* 標題統一設計 */
.education h2,
.experience h2,
.projects h2,
.Certifications h2 {
  color: #1c4a75;
  margin-bottom: 40px;
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1px;
  position: relative;
  padding-bottom: 15px;
}

.education h2::after,
.experience h2::after,
.projects h2::after,
.Certifications h2::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #1c4a75 0%, #2c5f8d 100%);
}

/* Experience 區塊 - 時間軸風格 */
.experience h3 {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 10px;
  margin-top: 35px;
  font-size: 1.5rem;
  color: #2c5f8d;
  font-weight: 600;
  padding-left: 30px;
}

.experience h3::before {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 12px;
  height: 12px;
  background: #2c5f8d;
  border-radius: 50%;
  box-shadow: 0 0 0 4px rgba(44, 95, 141, 0.2);
}

.experience .location {
  display: block;
  margin-top: 5px;
  color: #8b7355;
  font-size: 0.9em;
  font-weight: 400;
}

.experience p {
  color: #333;
  line-height: 1.8;
  margin: 15px 0;
  padding-left: 30px;
  font-size: 1.1rem;
  font-weight: 500;
}

.experience ul {
  margin-top: 15px;
  padding-left: 50px;
  list-style: none;
}

.experience li {
  margin: 12px 0;
  line-height: 1.8;
  position: relative;
  padding-left: 20px;
  color: #555;
}

.experience li::before {
  content: "•";
  position: absolute;
  left: 0;
  color: #2c5f8d;
  font-weight: bold;
  font-size: 1.2em;
}

/* Education 區塊 */
.education p {
  color: #333;
  line-height: 2;
  margin: 20px 0;
  font-size: 1.1rem;
}

.education strong {
  color: #1c4a75;
  font-weight: 600;
  font-size: 1.15rem;
}

/* Projects 區塊 */
.projects h3 {
  margin-top: 35px;
  margin-bottom: 15px;
  font-size: 1.4rem;
  font-weight: 600;
}

.projects h3 a {
  color: #1c4a75;
  text-decoration: none;
  transition: color 0.3s ease;
  border-bottom: 2px solid transparent;
  padding-bottom: 2px;
}

.projects h3 a:hover {
  color: #2c5f8d;
  border-bottom-color: #2c5f8d;
}

.projects ul {
  list-style: none;
  padding-left: 20px;
}

.projects li {
  margin: 12px 0;
  line-height: 1.8;
  position: relative;
  padding-left: 20px;
  color: #555;
}

.projects li::before {
  content: "→";
  position: absolute;
  left: 0;
  color: #2c5f8d;
  font-weight: bold;
}

/* Certifications 區塊 */
.Certifications ul {
  list-style: none;
  padding: 0;
}

.Certifications li {
  padding: 18px 0;
  margin: 0;
  border-bottom: 1px solid rgba(44, 95, 141, 0.1);
  transition: all 0.3s ease;
  line-height: 1.8;
}

.Certifications li:last-child {
  border-bottom: none;
}

.Certifications li:hover {
  padding-left: 15px;
  background: rgba(44, 95, 141, 0.03);
}

.Certifications a {
  color: #1c4a75;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.Certifications a:hover {
  color: #2c5f8d;
}

/* 深色模式 */
body.dark-mode .education,
body.dark-mode .experience,
body.dark-mode .projects,
body.dark-mode .Certifications {
  border-bottom-color: rgba(90, 155, 196, 0.2);
}

body.dark-mode .education h2,
body.dark-mode .experience h2,
body.dark-mode .projects h2,
body.dark-mode .Certifications h2 {
  color: #5a9bc4;
}

body.dark-mode .education h2::after,
body.dark-mode .experience h2::after,
body.dark-mode .projects h2::after,
body.dark-mode .Certifications h2::after {
  background: linear-gradient(90deg, #3d7aa8 0%, #5a9bc4 100%);
}

body.dark-mode .experience h3 {
  color: #5a9bc4;
}

body.dark-mode .experience h3::before {
  background: #5a9bc4;
  box-shadow: 0 0 0 4px rgba(90, 155, 196, 0.2);
}

body.dark-mode .education p,
body.dark-mode .experience p,
body.dark-mode .experience li,
body.dark-mode .projects li {
  color: #ccc;
}

body.dark-mode .education strong {
  color: #5a9bc4;
}

body.dark-mode .experience li::before,
body.dark-mode .projects li::before {
  color: #5a9bc4;
}

body.dark-mode .projects h3 a {
  color: #5a9bc4;
}

body.dark-mode .projects h3 a:hover {
  color: #7bb3d8;
  border-bottom-color: #7bb3d8;
}

body.dark-mode .Certifications li {
  border-bottom-color: rgba(90, 155, 196, 0.15);
}

body.dark-mode .Certifications li:hover {
  background: rgba(90, 155, 196, 0.05);
}

body.dark-mode .Certifications a {
  color: #5a9bc4;
}

body.dark-mode .Certifications a:hover {
  color: #7bb3d8;
}

/* 響應式設計 */
@media (max-width: 768px) {
  .resumepage {
    padding: 20px 0;
  }

  .education,
  .experience,
  .projects,
  .Certifications {
    padding: 40px 0;
  }

  .education h2,
  .experience h2,
  .projects h2,
  .Certifications h2 {
    font-size: 2rem;
  }

  .experience h3 {
    font-size: 1.3rem;
    padding-left: 25px;
  }

  .experience p {
    padding-left: 25px;
  }

  .experience ul {
    padding-left: 40px;
  }
}
