/* Header.css */
@import url("https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap");

header {
  padding: 40px 0;
  text-align: center;
  margin-top: 20px;
}

header .top {
  margin: 0 auto;
  width: 100%;
  max-width: none;
  padding: 60px 30px;
  background: transparent;
  transition: all 0.3s ease;
  position: relative;
}

header h2 {
  font-family: "Amatic SC", sans-serif;
  margin-bottom: 20px;
  font-size: 4rem;
  font-weight: 700;
  background: linear-gradient(135deg, #1c4a75 0%, #2c5f8d 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
}

header h3 {
  font-family: "Amatic SC", sans-serif;
  margin-top: 0;
  color: #666;
  font-size: 2rem;
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

header h3 {
  position: relative;
  z-index: 1;
}

/* 深色模式 */
body.dark-mode header .top {
  background: transparent;
}

body.dark-mode header h2 {
  background: linear-gradient(135deg, #3d7aa8 0%, #5a9bc4 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

body.dark-mode header h3 {
  color: #ccc;
}

/* 響應式設計 */
@media (max-width: 768px) {
  header .top {
    width: 90%;
    padding: 30px 20px;
  }

  header h2 {
    font-size: 3rem;
  }

  header h3 {
    font-size: 1.5rem;
    min-height: 50px;
  }
}
