.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid rgba(44, 95, 141, 0.3);
  background: #f0ebe0;
  color: #2c5f8d;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  z-index: 1000;
}

.theme-toggle:hover {
  transform: scale(1.1) rotate(20deg);
  background: #e8e0d0;
  box-shadow: 0 4px 12px rgba(44, 95, 141, 0.15);
  border-color: rgba(44, 95, 141, 0.5);
}

.theme-icon {
  width: 24px;
  height: 24px;
  transition: transform 0.3s ease;
}

.theme-toggle:active .theme-icon {
  transform: scale(0.9);
}

/* 深色模式樣式 */
body.dark-mode .theme-toggle {
  background: #1b263b;
  border-color: rgba(90, 155, 196, 0.4);
  color: #5a9bc4;
}

body.dark-mode .theme-toggle:hover {
  background: #0d1b2a;
}
