/* 霧面玻璃 & 水滴效果全局樣式 */

/* 玻璃卡片基礎樣式 */
.glass-card {
  background: rgba(255, 255, 255, 0.65);
  backdrop-filter: blur(15px) saturate(120%);
  -webkit-backdrop-filter: blur(15px) saturate(120%);
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow:
    0 4px 20px rgba(0, 0, 0, 0.06),
    inset 0 1px 0 rgba(255, 255, 255, 0.8);
  transition: all 0.3s ease;
}

.glass-card:hover {
  background: rgba(255, 255, 255, 0.75);
  box-shadow:
    0 6px 30px rgba(44, 95, 141, 0.12),
    inset 0 1px 0 rgba(255, 255, 255, 0.9);
  transform: translateY(-3px);
}

/* 水滴效果 */
.water-drop {
  position: relative;
  overflow: hidden;
}

.water-drop::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle at center,
    rgba(44, 95, 141, 0.08) 0%,
    rgba(28, 74, 117, 0.04) 20%,
    transparent 40%
  );
  animation: waterRipple 6s ease-in-out infinite;
  pointer-events: none;
}

@keyframes waterRipple {
  0%, 100% {
    transform: translate(0, 0) scale(1);
    opacity: 0.3;
  }
  50% {
    transform: translate(10px, 10px) scale(1.1);
    opacity: 0.6;
  }
}

/* 水滴反光效果 */
.water-reflection {
  position: relative;
}

.water-reflection::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.4) 0%,
    transparent 20%,
    transparent 80%,
    rgba(255, 255, 255, 0.2) 100%
  );
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.water-reflection:hover::after {
  opacity: 1;
}

/* 深色模式玻璃效果 */
body.dark-mode .glass-card {
  background: rgba(27, 38, 59, 0.4);
  border-color: rgba(65, 90, 119, 0.3);
  box-shadow:
    0 6px 28px rgba(0, 0, 0, 0.35),
    inset 0 1px 0 rgba(65, 90, 119, 0.15);
}

body.dark-mode .glass-card:hover {
  background: rgba(27, 38, 59, 0.5);
  box-shadow:
    0 8px 36px rgba(44, 95, 141, 0.25),
    inset 0 1px 0 rgba(65, 90, 119, 0.25);
}
